* {
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;
  }
  
  body {
    height: 100vh;
    max-width: 500px;
    display: grid;
    place-content: center;
  }
  
  /* calculator container */
  
  #calculator-container {
    margin: auto;
    height: 600px;
  
    border: 2px solid transparent;
    border-radius: 5px;
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    padding: 0.625rem;
    position: absolute;
    top: 50%;
    left: 50%;
    translate: -50% -50%;
    box-shadow: 0px 3px 6px 0px rgba(0, 0, 0, 0.15), 0px 2px 4px 0px rgba(0, 0, 0, 0.12);
    ;
    background-color: hsla(203, 11%, 29%, 0.856);
  }
  
  
  /* display */
  
  .display {
    font-size: 2rem;
    text-align: right;
    height: 5rem;
    width: 100%;
    border: 1px solid black;
    border-radius: 5px;
    background-color: aliceblue;
  }
  
  /* box-stylling */
  .box {
    font-size: 1.5rem;
    font-weight: 600;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    height: 3.125rem;
    width: 24%;
    border: 1px solid black;
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  /* Numbers and Operators  */
  
  .number { 
    background-color: whitesmoke;

  }
  
  .oprator {
  
    background: hsl(207, 19%, 61%);
  
  }
  
  
  .equal {
    background: hsl(355, 78%, 56%);
  }
  
  /* create responsive calculator */
  @media screen and (max-width:600px) {
    body {
        background-image: none;
        background-color: rgb(110, 90, 90);
    }
  
    #calculator-container {
        min-width: 21.875rem;
        min-height: 21.875rem;
        margin-left: auto;
        margin-right: auto;
  
    }
  }
  
  @media screen and (min-width:667px) and (orientation:landscape) {
    body {
        height: 100vh;
    }
  
    #calculator-container {
        max-height: 100%;
        max-width: 500px;
    }
  
    .display {
        max-height: 3rem;
    }
  }